import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Modal from '@material-ui/core/Modal';
import Backdrop from '@material-ui/core/Backdrop';
import Fade from '@material-ui/core/Fade';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import './style.scss'


const useStyles = makeStyles((theme) => ({
  modal: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  paper: {
    backgroundColor: theme.palette.background.paper,
    border: '2px solid #000',
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
  },
}));

export const TransitionsModal = () => {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <div className='Modal__password'>
      <button type="button" onClick={handleOpen}>
          i mt khu
      </button>
      <Modal
        aria-labelledby="transition-modal-title"
        aria-describedby="transition-modal-description"
        className={classes.modal}
        open={open}
        onClose={handleClose}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
        }}
      >
        <Fade in={open}>
          <div className={classes.paper}>
              <div className="Modal__password">
                <div className="Modal__title">
                  <h3>Thay i mt khu ca bn</h3>
                </div>
                <div className="password__change">
                    <h3>Nhp mt khu hin ti ca bn</h3>
                    <TextField id="outlined-basic" label="Mt khu hin ti..." variant="outlined" />
                </div>
                <div className="content">
                    <div className="password__new">
                        <h3>Nhp mt khu mi ca bn</h3>
                        <TextField id="outlined-basic" label="Mt khu mi..." variant="outlined" />
                        <TextField id="outlined-basic" label="Xc nhn mt khu mi..." variant="outlined" />
                    </div>
                </div>
                <div className="submit">
                    <Button variant="outlined">Hy b</Button>
                    <Button variant="outlined">Cp nht</Button>
                  </div>
              </div>
          </div>
        </Fade>
      </Modal>
    </div>
  );
}
